.modal_window {
  width: 100%;
  max-width: 720px;
  min-height: 500px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: var(--background);
  box-sizing: border-box;
  box-shadow: var(--box-shadow);
  border-radius: 40px;
  text-align: center;
  z-index: 2;
}

.modal_close {
  position: absolute;
  top: 60px;
  right: 40px;
  display: flex;
  align-items: center;
  padding: 0;
  border: none;
  background: var(--background);
  cursor: pointer;
  z-index: 3;

  svg {
    fill: var(--text-primary-color);
  }
}

.modal_header {
  margin: 7px 0;
  text-align: left;
}

@media only screen and (max-width: 767px) {
  .modal_window {
    max-width: none;
    height: 100vh;
    min-height: auto;
    top: 0;
    left: 0;
    transform: none;
    border-radius: 0;
    background: var(--background-alt);
    overflow: auto;
  }

  .modal_close {
    top: 20px;
    right: 8px;
    background: var(--background-alt);
  }
}
